<template>
<div class="main">
    <ul>
        <li @click="toPage('List')">
            <span>
                <i class="iconfont list_icon"></i>
            </span>
            <div>数据统计</div>
        </li>
        <li @click="toPage('AreaList')">
            <span>
                <i class="iconfont system_icon"></i>
            </span>
            <div>工区管理</div>
        </li>
    </ul>
</div>
</template>
<script>
export default {
  data () {
    return {
    }
  },
  methods: {
    toPage (pathName) {
      this.$router.push({name: pathName})
    }
  }
}
</script>
<style lang="scss" scoped>
.main {
    text-align: center;
    ul {
        margin-top: 30px;
    }
    li {
        margin-right: 20%;
        display: inline-block;
        span {
            border: 1px solid #ddd;
            width: 70px;
            height: 70px;
            display: flex;
            align-items: center;
            margin-bottom: 20px;
        }
        i {
            color: #999;
            margin: 0 auto;
        }
        div {
            font-size: 18px;
        }
    }
    li:last-child {
        margin-right: 0;
    }
}
</style>



